<template>
  <!--브랜드 검색결과 선택-->
  <ul class="chatStoreType02">
    <li v-for="(item, index) in parsedData" v-bind:key="index">
      <a @click="click(item)" :class="isOnConsulting ? 'cb-disabled-button' : ''" role="button">
        <b>{{ item.brandNm }}</b>
        <span>{{ getStoreNames(item.storNm) }}</span>
      </a>
    </li>
    <li v-if="hasMore">
      <a
        @click="more"
        class="stMoreBtns"
        role="button"
      >
        {{ $t("common.더보기") }}
      </a>
    </li>
  </ul>
  <!--//브랜드 검색결과 선택-->
</template>

<script>
import { userChatHandler } from '@/utils/chatbotChatHandler';
import consultingStatus from '@/mixins/consultingStatus';

export default {
  name: 'BrandSearchResult',
  mixins: [consultingStatus],
  props: {
    data: Object,
  },
  data() {
    return {
      max: 5,
    };
  },
  computed: {
    hasMore() {
      return this.data.list.length > this.max;
    },
    parsedData() {
      if (this.data.list.length > this.max) {
        return this.data.list.slice(0, this.max);
      }

      return this.data.list;
    },
  },
  methods: {
    getStoreNames(storeNameList) {
      return storeNameList.join(' ');
    },
    more() {
      if (this.isOnConsulting) {
        return;
      }

      this.max += 5;
      this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_210);
    },
    click(item) {
      if (this.isOnConsulting) {
        return;
      }

      this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_209);

      const message = item.brandNm + this.data.postfix;
      this.send(item);
      this.$store.commit('ADD_CONVERSATION', userChatHandler(message));
    },
    send(item) {
      this.$store.dispatch('SEND_MESSAGE', {
        message: 'BRAND_SEARCH_RESULT',
        hidden: true,
        options: {
          direct: true,
          from: this.$dictionary.elementType.BRAND_SEARCH_RESULT,
          item,
          intent: this.data.intent,
        },
      });
    },
  },
};
</script>